import tkiFloatKeyboard from "@/components/tki-float-keyboard/tki-float-keyboard.vue";
export default {
	components: {
		tkiFloatKeyboard
	},
	data() {
		return {
			carIndex: -1,
			carInput: [{
					type: 2,
					val: "云"
				},
				{
					type: 4,
					val: "A"
				},
				{
					type: 1,
					val: ""
				},
				{
					type: 1,
					val: ""
				},
				{
					type: 1,
					val: ""
				},
				{
					type: 1,
					val: ""
				},
				{
					type: 3,
					val: ""
				},
				{
					type: 1,
					val: ""
				}
			],
			keyType: 0,
			isPower: false, // 新能源
			cardCur: 0,
			dotStyle: false,
			swiperList: [{
					id: 0,
					url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big84000.jpg',
					title: '轿车'
				},
				{
					id: 1,
					url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg',
					title: 'SUV'
				},
				{
					id: 2,
					url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big39000.jpg',
					title: 'MPV'
				}
			]
		};
	},
	methods: {
		// 判定是否为空
		empty(v) {
			let tp = typeof v,
				rt = false;
			if (tp == "number" && String(v) == "") {
				rt = true
			} else if (tp == "undefined") {
				rt = true
			} else if (tp == "object") {
				if (JSON.stringify(v) == "{}" || JSON.stringify(v) == "[]" || v == null) rt = true
			} else if (tp == "string") {
				if (v == "" || v == "undefined" || v == "null" || v == "{}" || v == "[]") rt = true
			} else if (tp == "function") {
				rt = false
			}
			return rt
		},
		inputKey(e) {
			let that = this;
			let data = e.currentTarget.dataset;
			if (data.index >= 7) {
				if (!that.isPower) {
					return false;
				} else {
					that.carIndex = 7;
				}
			} else {
				that.carIndex = data.index;
			}
			that.upKeyType();
			that.keyShow();
		},
		keyCbVal(e) {
			let that = this;
			let index = Number(that.carIndex);
			if (index >= 0 && index < 6) {
				that.carInput[index].val = e;
				that.carIndex = index + 1;
			} else if (index == 6) {
				that.carInput[index].val = e;
				if (that.isPower) {
					// 新能源
					that.carIndex = 7;
				} else {
					// 不是新能源出输入结束
					that.keyHide();
					that.carIndex = -2;
					console.log('非新能源车输入完毕')
				}
			} else if (index == 7) {
				// 新能源车输入完毕
				that.carInput[index].val = e;
				that.keyHide();
				that.carIndex = -3;
				console.log('新能源车输入完毕')
			}
			that.upKeyType();
		},
		keyCbDel(e) {
			let index = this.carIndex;
			if (index > 0) {
				if (!this.empty(this.carInput[index].val)) {
					this.carInput[index].val = "";
					this.carIndex = index;
				} else {
					this.carInput[index - 1].val = "";
					this.carIndex = index - 1;
				}
			}
			this.upKeyType();
		},
		upKeyType() {
			if (!this.empty(this.carInput[this.carIndex])) {
				this.keyType = this.carInput[this.carIndex].type;
			}
		},
		keyShow() {
			this.$refs.keybd._keyShow();
		},
		keyHide() {
			this.$refs.keybd._keyHide();
		},
		keyCbHide() {
			if (this.carIndex != -3 || this.carIndex != -2) {
				this.carIndex = -1;
			}
		},
		powerChange(e) {
			let that = this
			let i = that.checkCar().i
			if (e.detail.value.length > 0) {
				that.isPower = true;
				if (i == -1) {
					that.carIndex = 7;
				} else {
					that.carIndex = i;
				}
				that.keyShow();
			} else {
				that.isPower = false;
				that.carInput[7].val = "";
				if (that.carIndex == 7) {
					that.keyHide();
					that.carIndex = -2;
				}
			}
			that.upKeyType();
		},
		checkCar() {
			// 检查车牌是否输入完成
			let that = this;
			let i = 7;
			let rt = {
				i: -1,
				isempty: false,
				val: ""
			};
			if (that.isPower) {
				i = 8;
			}
			for (let index = 0; index < i; index++) {
				const obj = that.carInput[index];
				if (this.empty(String(obj.val))) {
					rt.i = index;
					rt.isempty = true;
					rt.val = "";
					break;
				}
				rt.val += that.carInput[index].val;
			}
			return rt;
		},
		toBind() {
			let that = this
			let ck = that.checkCar();
			if (ck.i == -1 && !ck.isempty) {
				console.log('可以绑定车牌了')
			} else {
				// 显示键盘输入
				that.keyShow();
				that.carIndex = ck.i;
				that.keyType = that.carInput[ck.i].type
			}
		},
		// cardSwiper
		cardSwiper(e) {
			this.cardCur = e.detail.current;
			console.log('当前card：' + this.cardCur);
		}
	},
	mounted() {},
	onPageScroll: function() {
		let that = this;
	},
	onReachBottom: function() {
		let that = this;
	},
	onPullDownRefresh: function() {
		let that = this;
	},
	onShow: function() {
		let that = this;
	},
	onHide: function() {
		let that = this;
	},
	onUnload: function() {
		let that = this;
	},
	onLoad: async function() {
		let that = this;
	}
};
